.setting {
 
}

.settingOpen {
    transform: scaleX(1);
}

.city {
    display: flex;
    background-color: lightgray;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    border: 1px solid red;
}

.block {
    display: flex;
    align-items: center;
}

.burger {
    display: inline-block;
    margin-right: 20px;
    padding: 10px;
    border: 1px solid red;
}

.close {
    position: absolute;
    top: 10px;
    right: 10px;
}

.deleteCity {
    border: 1px solid red;
}